





















































































































































body{
  &::before, &::after{
    background:transparent;
  }
}
.welcome-view{
  @media screen and (max-width: 991px){
    padding: 0 2rem;
    box-sizing: border-box;
  }
}

.loadin-content{
    max-width:500px;
    width: 100%;
    margin:0 auto;
    border:1px solid #dddddd;
    border-radius: 10px;
    padding: 2rem;
    box-sizing: border-box;
    .el-input{
        margin-bottom: 1.5rem;
        margin-top: .5rem;
    }
}
.underMv{
    margin-bottom: 7vw;
    margin-top: 0;
    @media screen and (max-width: 991px){
      margin-top: 2rem; 
      padding-top: 0;
    }
    @media screen and (max-width: 414px){
      margin-top: 0; 
    }
}
.el-button{
    width: 100%;
    border: 0;
    @media screen and (max-width: 991px){
      background-color:black;
      color: white;
    }
}

.el-dialog__wrapper{
  
}

.el-dialog__body{
  text-align: center;
}
